* {
  font-family: Lato;
  box-sizing: border-box;
}
.spanInputCheckBox {
  width: 50px;
  display: flex;
  height: 50px;
  justify-content: center;
  align-items: center;
}
.input {
  display: flex;
  flex-flow: row nowrap;
  font-family: Lato;
  justify-content: flex-start;
  width: 100%;
}
.input--label {
  text-align: right;
  line-height: 30px;
  color: #727272;
  margin-right: 10px;
  text-overflow: clip;
}
.input--input {
  position: relative;
  display: flex;
}
.input--input input[type=number]::-webkit-inner-spin-button,
.input--input input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.input--input input {
  height: 30px;
  color: #2E7FAE;
  border: 1px #EDEDED solid;
  border-radius: 5px;
  outline: none;
  transition: all 250ms;
  line-height: 30px;
  border-radius: 4px;
  padding: 4px 10px;
  flex: 1;
}
.input--input input:focus {
  border-color: #2E7FAE;
}
.input--input input::placeholder {
  opacity: 0.5;
}
.input--input input.hasError {
  border-color: #D50A0A;
}
.input--input .InputError_PopUp {
  position: relative;
  width: 100%;
  height: 100%;
  color: #D50A0A;
  background-color: #ffffff;
  border: 1px solid #DDDDDD;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.25);
  font-family: LatoBold;
  transition: all 250ms;
  padding: 4px 10px;
  border-radius: 4px;
  font-size: 12px;
}
.input--input .InputError_PopUp::after {
  content: '';
  position: absolute;
  top: -5px;
  left: 5px;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-bottom: 5px solid #ffffff;
}
